import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";
import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    page: 1
  };
  moveNextPage = page => {
    const { firstPageForm } = this.state;
    if (page === 1) {
      this.setState({
        page: 2,
        checkMark: true
      });
    } else if (page === 2) {
      this.setState({
        page: 3,
        checkMark: true
      });
    } else if (page === 3) {
      this.setState({
        page: 4,
        checkMark: true
      });
    } else {
      this.setState({
        page: 5,
        checkMark: true
      });
    }
  };
  render() {
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li>
                Category and <br />
                location
                {/* {this.state.page === 2 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Topic content
                {/* {this.state.page === 3 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Media
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              <li>
                Preview
                {/* {this.state.page === 4 && <img src={checkMark} alt="check-mark" />} */}
              </li>
              ew
            </ul>
          </div>
          <div className="tab-content">
            {this.state.page === 1 ? (
              <LocationAndCategory moveNextPage={this.moveNextPage} />
            ) : this.state.page === 2 ? (
              <TopicContent moveNextPage={this.moveNextPage} />
            ) : this.state.page === 3 ? (
              <Media moveNextPage={this.moveNextPage} />
            ) : (
              <Preview moveNextPage={this.moveNextPage} />
            )}
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
